export default {
  data: () => ({
    cityId: null,
    pointId: null,
    cities: [
      "Уфа1",
      "Уральск1",
      "Увельский1",
      "Ульяновск1",
      "Уфа",
      "Уральск",
      "Увельский",
      "Ульяновск",
    ],
  }),
  computed: {
    stepOne: {
      get: function () {
        return this.step;
      },
      set(value) {
        this.$emit("update:step", value);
      },
    },
  },
};
