export default {
  data: () => ({
    drawer: null,
    menuItems: [
      {
        name: "Парковка",
        href: "/",
      },
      {
        name: "Страховка",
        href: "/",
      },
      {
        name: "Бензин",
        href: "/",
      },
      {
        name: "Обслуживание",
        href: "/",
      },
    ],
    socialLinks: [
      {
        name: "telegram",
        href: "/",
      },
      {
        name: "facebook",
        href: "/",
      },
      {
        name: "instagram",
        href: "/",
      },
    ],
  }),
  computed: {
    miniNavWidth() {
      if (this.$vuetify.breakpoint.smAndDown) {
        return 85;
      } else {
        return 64;
      }
    },
    navwidth() {
      if (this.$vuetify.breakpoint.smAndDown) {
        return 100;
      } else if (this.$vuetify.breakpoint.md) {
        return 60;
      } else {
        return 50;
      }
    },
  },
};
